.gallery {
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

img {
  width: 100%;
  height: auto;
  max-width: 400px;
  display: block;
}

.container {
  max-width: 270px;
  cursor: pointer;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  list-style: none;
  display: flex;
}

.gallery__title {
  text-align: center;
  color: #202025;
  font-size: 48px;
  font-weight: 700;
  line-height: 72px;
}

#pagination {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.prev, .next {
  width: 40px;
  height: 40px;
  color: #fe7031;
  cursor: pointer;
  background: #fdfdfd;
  border: 1px solid #eaebea;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.pagination-number {
  gap: 20px;
  display: flex;
}

.is-hidden {
  opacity: 0;
}

.btn-pagination {
  width: 40px;
  height: 40px;
  color: #fe7031;
  cursor: pointer;
  background: #fdfdfd;
  border: 1px solid #eaebea;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.active {
  color: #fdfdfd;
  background-color: #fe7031;
  border: none;
}

